import React, {Component} from 'react'
import loader from './loader.svg'
import Chart from 'chart.js'

function getRandomColor() {
  var letters = '0123456789ABCDEF';
  var color = '#';
  for (var i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
}


class Summary extends Component {
  constructor (props) {
    super(props);
  }

  renderContent () {
    const { loading } = this.props;

    if (loading) {
      return <div className="loader loader--style1" title="0">
        <img src={loader} alt="loader" />
      </div>;
    } else {
      return <h3>Distribuio</h3>
    }
  }

  componentDidUpdate (prevProps) {
    if (prevProps.loading && !this.props.loading) {
      const { holdings } = this.props;

      const colors = ["#D59FC3", "#86E516", "#0CAE43", "#D9D7FA", "#C9B62B", "#DBBB39", "#E0416B", "#4D1705", "#A84AE5", "#EF8F3F", "#83849B", "#070BF9", "#22604F", "#CBC2C9", "#CA4373", "#0A0E31", "#F73052"];

      new Chart(this._canvas,{
        type: 'pie',
        data: {
          datasets: [{
            data: holdings.map((hold) => hold['currentTotalValueBTC'] || hold['amount']),
            backgroundColor: colors,
          }],
          labels: holdings.map((hold) => hold['symbol'])
        },
        options: {}
      });
    }
  }

  render () {
    return (
      <div className="row">
        <div className="col">
          {this.renderContent()}
          <div className="chart-container">
            <canvas ref={(e) => this._canvas = e }></canvas>
          </div>
        </div>
      </div>
    );
  }
}

export default Summary;



