import React, {Component} from 'react'
import holdings from './lib/holdings'
import Value from './Value'

class WalletCoin extends Component {

  renderTotalValueDetails () {
    const { holding } = this.props;
    const result = holding['currentPriceBTC'] >= holding['priceBTC'];

    if (holding['symbol'] === 'BTC') {
      return;
    }

    return (
      <div className="row">
        <div className="col">
          <Value
            label="Preo total compra"
            value={` ${holding['totalBuyValueBTC'].toFixed(10)}`} />
        </div>
        <div className="col">
          <Value
            label="Preo total hoje"
            value={` ${holding['currentTotalValueBTC'].toFixed(10)}`} />
        </div>
      </div>
    );
  }

  renderBuyDetails () {
    const { holding } = this.props;
    const result = holding['currentPriceBTC'] >= holding['priceBTC'];

    if (holding['symbol'] === 'BTC') {
      return;
    }

    return (
      <div className="row">
        <div className="col">
          <Value
            label="Preo compra (UN)"
            value={` ${holding['priceBTC']}`} />
        </div>
        <div className={`col ${result ? 'positive' : 'negative'}`}>
          <Value
            label="Preo hoje (UN)"
            value={` ${holding['currentPriceBTC']} (${holding['evaluation'].toFixed(3)}%)`} />
        </div>
      </div>
    );
  }

  renderResult () {
    const { holding } = this.props;
    const result = holding['currentPriceBTC'] >= holding['priceBTC'];

    if (holding['symbol'] === 'BTC') {
      return;
    }

    return (
      <div className="row">
        <div className={`col ${result ? 'positive' : 'negative'}`}>
          <Value
            big
            label="Resultado"
            value={` ${(holding['result'] || 0).toFixed(10)}`} />
        </div>
      </div>
    );
  }

  render () {
    const { holding } = this.props;
    const result = holding['currentPriceBTC'] >= holding['priceBTC'];

    return (
      <div className="wallet-coin">
        <div className="coin-symbol">
          <span className='symbol-text'>{holding['symbol']}</span>
        </div>
        <div className="coin-data">
          <div className="row">
            <div className="col">
              <Value
                big
                label="Carteira"
                value={`${holding['amount']} ${holding['symbol']}`} />
            </div>
          </div>
          {this.renderBuyDetails()}
          {this.renderTotalValueDetails()}
          {this.renderResult()}
        </div>
      </div>
    );
  }
}

export default WalletCoin;



