import React, {Component} from 'react';

import WalletCoin from './WalletCoin';

class Wallet extends Component {

  constructor (props) {
    super(props);

    this.state = {
    }
  }

  render () {
    const { loading, holdings } = this.props;
    if (loading) {
      return null;
    }

    return (
      <div>
        <h3>Carteira</h3>
        {
          holdings.map((holding, i) =>
            <WalletCoin key={i} holding={holding} />)
        }
      </div>
    );
  }
}

export default Wallet;



