import { getQuotes } from './api'
import holdings from './holdings'
import { multiply, evaluation } from './util'
import Decimal from 'decimal.js'


const findQuoteForHolding = (quotes, holding) => {
  const quote = quotes.find((quote) => quote[0] === holding['ticker'])
  return quote
}

const buildWallet = () => {
  return getQuotes().then((quotes) => {
    return holdings.map((holding) => {
      if (holding['symbol'] != 'BTC') {
        const quote = findQuoteForHolding(quotes, holding)
        holding['currentPriceBTC'] = quote[7]
        holding['evaluation'] = evaluation(holding['priceBTC'], holding['currentPriceBTC'])
        holding['totalBuyValueBTC'] = multiply(holding['amount'], holding['priceBTC'])
        holding['currentTotalValueBTC'] = multiply(holding['amount'], holding['currentPriceBTC'])
        holding['result'] = holding['currentTotalValueBTC'] - holding['totalBuyValueBTC']
      }
      return holding;
    });
  })
}

export {
  buildWallet
}



