import React, { Component } from 'react'
import { buildWallet } from './lib/wallet'
import './App.css'

import Summary from './Summary'
import Wallet from './Wallet'
import Result from './Result'


class App extends Component {

  constructor (props) {
    super(props);

    this.state = {
      loading: true,
      holdings: [],
    }

    buildWallet().then((holdings) => {
      this.setState({
        loading: false,
        holdings: holdings,
      })
    })
  }

  render() {
    const { loading, holdings } = this.state;

    return (
      <div className="container">
        <header className='app-header'>
          <div className="row">
            <div className="col">
              <h1>1000x Cryptos</h1>
            </div>
          </div>
        </header>
        <Result loading={loading} holdings={holdings} />
        <Wallet loading={loading} holdings={holdings} />
        <Summary loading={loading} holdings={holdings} />
      </div>
    );
  }
}

export default App;



