
const decimalPlaces = (num) => {
  var match = (''+num).match(/(?:\.(\d+))?(?:[eE]([+-]?\d+))?$/);
  if (!match) { return 0; }
  return Math.max(
       0,
       // Number of digits right of decimal point.
       (match[1] ? match[1].length : 0)
       // Adjust for scientific notation.
       - (match[2] ? +match[2] : 0));
}


const multiply = (num1, num2) => {
  const num1dp = decimalPlaces(num1)
  const num2dp = decimalPlaces(num2)

  const num1pow = Math.pow(10, num1dp)
  const num2pow = Math.pow(10, num2dp)

  return (num1 * num1pow) * (num2 * num2pow) / (num1pow * num2pow)
}

const evaluation = (buyPrice, currentPrice) => {
  const buyd = decimalPlaces(buyPrice)
  const priced = decimalPlaces(currentPrice)

  let decimalCount;
  if (buyd > priced) {
    decimalCount = buyd;
  } else {
    decimalCount = priced;
  }

  const factor = Math.pow(10, decimalCount);

  const buyInteger = buyPrice * factor;
  const priceInteger = currentPrice * factor;

  return ((priceInteger - buyInteger) * 100) / buyInteger;
}

export {
  decimalPlaces,
  multiply,
  evaluation,
}



