import React, {Component} from 'react'
import Value from './Value'
import {evaluation} from './lib/util'
import {getBTCBRL} from './lib/api'

class Result extends Component {

  constructor (props) {
    super(props);

    this.state = {
      initialValue: 0.17683578,
      result: 0,
      sum: 0,
    };
  }

  componentWillReceiveProps (nextProps) {
    const sum = this.sumHoldings(nextProps.holdings)
    const result = evaluation(this.state.initialValue, sum)
    this.setState({sum, result})
  }

  sumHoldings(holdings) {
    let sum = 0;

    if (holdings && holdings.length) {
      holdings.map((holding) => {
        sum += (holding['currentTotalValueBTC'] || holding['amount'])
      })
    }

    return sum
  }

  render () {
    const { loading, holdings } = this.props;
    if (loading) {
      return null;
    }

    return (
      <div className='Result-cmp'>
        <h3>Resultado</h3>
        <div className="wallet-coin">
          <div className="coin-data">
            <div className='row'>
              <div className="col">
                <Value
                  big
                  label="BTC inicial"
                  value={` ${this.state.initialValue}`} />

                <div className={this.state.result >= 0 ? 'positive' : 'negative'}>
                  <Value
                    big
                    label="BTC hoje"
                    value={`  ${this.state.sum.toFixed(8)} (${this.state.result.toFixed(3)}%)`} />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Result;



