import React, {Component} from 'react';

class Value extends Component {
  constructor (props) {
    super(props);

    this.state = {
    }
  }

  render () {
    const { label, value, big } = this.props;

    return (
      <div className={`Value-cmp ${big && 'big'}`}>
        <p className='label'>{label}</p>
        <p className='value'>{value}</p>
      </div>
    );
  }
}

export default Value;



