import axios from 'axios'
import holdings from './holdings'
import { BlinkTradeRest } from 'blinktrade';


const QUOTES_URL = "https://api.bitfinex.com/v2/tickers";

const buildSymbolsQueryString = () => holdings
  .map((holding) => holding.ticker)
  .join(',')

const buildQuotesUrl = () => QUOTES_URL + '?symbols=' + buildSymbolsQueryString()

const getQuotes = () => axios
  .get(buildQuotesUrl())
  .then((response) => response.data)

const getBTCBRL = () => {
  // axios.get("https://api.blinktrade.com/api/v1/BRL/ticker?callback=heydjow")
  //   .then((response) => response.data)
  //   .then(console.log)
  const BlinkTrade = new BlinkTradeRest({
    currency: 'BRL',
    prod: false
  })

  BlinkTrade.ticker().then(console.log)

}

export {
  getQuotes,
  getBTCBRL
};



